<template>
  <div>
    <div>
      <div class="navbar-static-top">
        <topbar></topbar>
      </div>
      <div class="col-sm-2">
        <leftmenu></leftmenu>
      </div>
      <div class="col-sm-10" >
        <router-view :stateid="stateid" :statetitle="statetitle"></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import '@/assets/css/main.css'
import "@/assets/css/button.css"
import topbar from '@/components/topbar.vue'
import leftmenu from '@/components/leftmenu.vue'
  export default {
    data:()=>{
      return {
        stateid:"",
        statetitle:""
      }
    },
   components:{
      topbar,
    leftmenu
   },
   mounted:function(){
     this.loginState();
   },
   methods:{
     loginState(){
       var vm=this;
     var state=vm.$store.getters.GETUSERSTATE;
      this.stateUpdateWatch(state.stateid,state.statetitle);
     },
     stateUpdateWatch(name,title) {
       var vm=this;
       vm.stateid=name;
       vm.statetitle=title;
     }
   },
    watch: {
    $route(to, from) {
      //   // 对路由变化作出响应...
      this.loginState();
      this.$validator.errors.clear(); //清除表单错误
    },
    componentId(to, from) {
      this.childId = to;
    }
  }
  };

</script>
<style scoped>


</style>



