<template>
  <div class="list-group">
    <a class="list-group-item list-group-item-heading list-item-head">模块管理</a>
    <ul>
      <li v-for="(menu,index) in menus" :key="index">
        <button @click="changeMenu(index,menu,$event)" v-if="menu.pageInf.type === 0" :class="['list-group-item btn btn-link menu-list',{active:menu.pageInf.show}]">
          <span>{{menu.pageInf.pagename}}</span>
        </button>
        <ul :class="['ul-item',{block:menu.pageInf.show,none:!menu.pageInf.show}]" >
          <li v-for="list of menu.list" :key="list.pageInf.id">
            <router-link class="ul-a" active-class="active" :to="'/main/'+list.pageInf.url" v-text="list.pageInf.pagename"></router-link>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      menus: []
    };
  },
  created: function() {
    var vm = this;
    vm.loginmenu(vm);
  },
  methods: {
    addShow(Obj) {
      function add(Obj) {
        var obj = Obj;
        for (var i = 0; i < obj.length; i++) {
          if (obj[i].pageInf) {
            obj[i].pageInf.show = false;
          }
          if (obj[i].list != null) {
            if (obj[i].list.length > 0) {
              add(obj[i].list);
            }
          }
        }
        return obj;
      }
      return add(Obj);
    },
    loginmenu: function(vm) {
      var userpage = vm.$store.getters.GETUSERPAGE;
      if (userpage != null && userpage.length > 0) {
        vm.menus = userpage;
      } else {
        vm.$axios
          .post(vm.HOST + "selectUserPage.action")
          // .get("static/json/menu.json")
          .then(function(response) {
            var res = response.data;
            if (res.code == "0") {
              vm.menus = vm.addShow(res.userPageList);
              vm.$store.commit("UPDATE_USERPAGE", vm.menus);
              vm.$store.commit("UPDATE_USERPERMS",res.perms);
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    changeMenu(index, item, e) {
      //  var event=e.target.nextSibling.nextSibling;
      this.menus.forEach(i => {
        // 判断如果数据中的headerData[i]的show属性不等于当前数据的show属性那么headerData[i]等于false
        if (i.pageInf.show !== this.menus[index].pageInf.show) {
          i.pageInf.show = false;
        }
      });
      item.pageInf.show = !item.pageInf.show;
    }
  }
};
</script>
<style scoped>
.active {
  z-index: 2;
  color: #fff;
  background-color: #337ab7 !important;
  border-color: #f1f3f5;
}
.menu-list:hover,
.menu-list.active {
  background: #bdbfc0 !important;
}
ul {
  list-style-type: none;
  padding: 0;
}
.ul-item {
  display: none;
}
.block {
  display: block;
}
.none {
  display: none;
}
.ul-a {
  height: 3rem;
  line-height: 3rem;
  width: 100%;
  display: block;
  text-decoration: none;
  text-align: center;
  background: #ccc;
  color: #000;
}
.ul-a:hover {
  color: #fff !important;
  background: #3f688b !important;
}
</style>



